import Image from "next/image";

const headerHeight = "4px"; // Define the header height

export default function Home() {
  return (
    <div
      className="grid grid-rows-[var(--header-height)_1fr_20px] items-center justify-items-center min-h-screen font-[var(--font-geist-sans)]"
    >
      <header className="bg-amber-700 flex items-center justify-between w-full h-full text-sm sm:text-base font-medium row-start-1">
        <div className="logo">
          <Image
            className="dark:invert"
            src="/logo.svg" // Ensure this file exists in the /public directory
            alt="Logo"
            width={32}
            height={32}
            priority // Add priority to ensure it loads quickly
          />
          <span className="ml-2">Marakushi</span>
        </div>
        <div className="menu"></div>
        <div className="cta">
          <button>Contact</button>
        </div>
      </header>
      <main className="flex flex-col gap-[var(--spacing-32)] row-start-2 items-center sm:items-start">
        {/* Content */}
      </main>
      <footer className="row-start-3 flex gap-[var(--spacing-24)] flex-wrap items-center justify-center">
        {/* Footer */}
      </footer>
    </div>
  );
}